:root {
  --primary-color: #1a365d;
  --secondary-color: #2b6cb0;
  --accent-color: #c53030;
  --text-light: #fff;
  --text-dark: #1a202c;
  --bg-light: #f7fafc;
}

body {
  color: var(--text-dark);
  padding-top: 56px;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

.hero-section {
  height: 100vh;
  color: var(--text-light);
  background-color: #0000;
  background-image: linear-gradient(#1a365dd9, #2b6cb0d9), url("hero-bg.2a63240c.jpg");
  background-position: center;
  background-repeat: repeat, repeat;
  background-size: cover;
  background-attachment: fixed;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  padding: 120px 0 60px;
  position: relative;
  overflow: hidden;
}

.hero-overlay {
  z-index: 1;
  background: radial-gradient(circle, #1a365db3 0%, #2b6cb0e6 100%);
  position: absolute;
  inset: 0;
}

.hero-section .container {
  z-index: 2;
  position: relative;
}

.hero-section .display-4 {
  text-shadow: 2px 2px 4px #0000004d;
  font-size: 3.5rem;
  font-weight: 700;
  animation: 1s ease-out .5s both fadeInUp;
}

.hero-section .lead {
  text-shadow: 1px 1px 2px #0003;
  font-size: 1.8rem;
  font-weight: 300;
  animation: 1s ease-out .7s both fadeInUp;
}

.hero-section .btn-light {
  transition: transform .3s, box-shadow .3s;
  animation: 1s ease-out 1s both fadeInUp;
}

.hero-section .btn-light:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px #0003;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.contact-info {
  color: var(--text-dark);
  font-size: .9rem;
}

.contact-info i {
  color: var(--secondary-color);
  margin-right: .5rem;
}

.skills-list {
  padding-left: 0;
  list-style: none;
}

.skills-list li {
  margin-bottom: .5rem;
  padding-left: 1.5rem;
  position: relative;
}

.skills-list li:before {
  content: "•";
  color: var(--secondary-color);
  font-weight: bold;
  position: absolute;
  left: 0;
}

.timeline .card {
  border-left: 4px solid var(--secondary-color);
  margin-bottom: 1.5rem;
  margin-left: 20px;
}

.timeline .card-title {
  color: var(--primary-color);
}

.contact-info-large i {
  color: var(--secondary-color);
}

.contact-info-large h6 {
  color: var(--primary-color);
  font-weight: 600;
}

.contact-form {
  box-shadow: 0 0 20px #0000001a;
}

.form-label {
  color: var(--text-dark);
  font-weight: 500;
}

.form-control {
  border: 2px solid #cbd5e0;
  margin-bottom: 1rem;
  transition: all .3s;
}

.btn-primary {
  background-color: var(--secondary-color);
  border-color: var(--secondary-color);
}

.btn-primary:hover, .btn-primary:focus {
  background-color: var(--primary-color);
  border-color: var(--primary-color);
}

.experience-list {
  padding-left: 1.2rem;
}

.experience-list > li {
  margin-bottom: 1rem;
}

.experience-list ul {
  margin-top: .5rem;
  padding-left: 1.5rem;
}

.experience-list ul li {
  color: var(--text-dark);
  margin-bottom: .3rem;
}

.achievement-list, .expertise-list {
  padding-left: 0;
  list-style: none;
}

.achievement-list > li, .expertise-list > li {
  margin-bottom: 1rem;
  padding-left: 1.5rem;
  position: relative;
}

.achievement-list > li:before {
  content: "🏆";
  position: absolute;
  left: 0;
}

.expertise-list > li:before {
  content: "⚡";
  position: absolute;
  left: 0;
}

.achievement-list ul, .expertise-list ul {
  margin-top: .5rem;
  padding-left: 1.5rem;
  list-style: none;
}

.achievement-list ul li, .expertise-list ul li {
  margin-bottom: .3rem;
  padding-left: 1rem;
  position: relative;
}

.achievement-list ul li:before, .expertise-list ul li:before {
  content: "•";
  color: var(--secondary-color);
  position: absolute;
  left: 0;
}

.card-title {
  border-bottom: 2px solid var(--secondary-color);
  margin-bottom: 1rem;
  padding-bottom: .5rem;
}

section {
  opacity: 0;
  animation: .8s forwards fadeInUp;
  transform: translateY(20px);
}

html {
  scroll-behavior: smooth;
}

.skip-link {
  background: var(--primary-color);
  color: #fff;
  z-index: 100;
  padding: 8px;
  transition: top .3s;
  position: absolute;
  top: -40px;
  left: 0;
}

.skip-link:focus {
  top: 0;
}

a:focus, button:focus, input:focus, textarea:focus {
  outline: 3px solid var(--secondary-color);
  outline-offset: 2px;
}

.text-muted {
  color: #4a5568 !important;
}

a {
  text-underline-offset: 2px;
  text-decoration-thickness: 1px;
}

a:hover {
  text-decoration-thickness: 2px;
}

@media (prefers-reduced-motion: reduce) {
  * {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

.btn-icon {
  position: relative;
}

.btn-icon:after {
  content: attr(aria-label);
  background: var(--text-dark);
  color: var(--text-light);
  opacity: 0;
  visibility: hidden;
  border-radius: 4px;
  padding: .5rem;
  font-size: .875rem;
  transition: opacity .2s, visibility .2s;
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.btn-icon:hover:after, .btn-icon:focus:after {
  opacity: 1;
  visibility: visible;
}

.badge {
  border-radius: 25px;
  padding: .5rem 1rem;
  font-size: 1rem;
  font-weight: 500;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 15px #0000001a;
}

.badge:before {
  content: "";
  background: linear-gradient(90deg, #0000, #fff3, #0000);
  width: 100%;
  height: 100%;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

.badge:hover:before {
  left: 100%;
}

.badge:hover {
  transform: translateY(-3px)scale(1.05);
  box-shadow: 0 6px 20px #00000026;
}

.badge.bg-secondary {
  background: linear-gradient(135deg, #4a5568 0%, #718096 100%) !important;
}

.badge.bg-info {
  background: linear-gradient(135deg, #0d6efd 0%, #0dcaf0 100%) !important;
}

.badge.bg-success {
  background: linear-gradient(135deg, #198754 0%, #20c997 100%) !important;
}

.badge.bg-primary {
  background: linear-gradient(135deg, #1a365d 0%, #2b6cb0 100%) !important;
}

.card {
  border: none;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 6px #0000001a;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 15px #0003;
}

.card:after {
  content: "";
  border-radius: inherit;
  opacity: 0;
  background: radial-gradient(circle, #ffffff1a 0%, #0000 70%);
  width: 100%;
  height: 100%;
  transition: opacity .3s;
  position: absolute;
  top: 0;
  left: 0;
}

.card:hover:after {
  opacity: 1;
}

[data-scroll] {
  opacity: 0;
  transition: all .8s;
  transform: translateY(20px);
}

[data-scroll].visible {
  opacity: 1;
  transform: translateY(0);
}

.section-header {
  display: inline-block;
  position: relative;
}

.section-header:after {
  content: "";
  background: var(--secondary-color);
  width: 0;
  height: 3px;
  transition: width .3s;
  position: absolute;
  bottom: -10px;
  left: 0;
}

.section-header:hover:after {
  width: 100%;
}

.contact-info-large i {
  animation: 2s infinite pulse;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

.skills-list li {
  transition: transform .3s;
}

.skills-list li:hover {
  transform: translateX(10px);
}

.timeline .card {
  position: relative;
}

.timeline .card:before {
  content: "";
  background: var(--secondary-color);
  border-radius: 50%;
  width: 16px;
  height: 16px;
  transition: transform .3s;
  position: absolute;
  top: 20px;
  left: -10px;
  transform: scale(0);
}

.timeline .card:hover:before {
  transform: scale(1);
}

.skill-bar {
  background: #e9ecef;
  border-radius: 5px;
  height: 20px;
  margin-bottom: 20px;
  position: relative;
  overflow: hidden;
}

.skill-progress {
  background: var(--secondary-color);
  border-radius: 5px;
  width: 0;
  height: 100%;
  transition: width 1.5s ease-in-out;
  position: absolute;
}

.navbar {
  backdrop-filter: blur(10px);
  background: #ffffffe6;
  box-shadow: 0 2px 10px #0000001a;
}

.nav-link {
  padding: .5rem 1rem;
  position: relative;
}

.nav-link:after {
  content: "";
  background: var(--secondary-color);
  width: 0;
  height: 2px;
  transition: all .3s;
  position: absolute;
  bottom: 0;
  left: 50%;
}

.nav-link:hover:after {
  width: 100%;
  left: 0;
}

.btn {
  z-index: 1;
  transition: all .3s;
  position: relative;
  overflow: hidden;
}

.btn:before {
  content: "";
  z-index: -1;
  background: #ffffff1a;
  border-radius: 50%;
  width: 0;
  height: 0;
  transition: width .6s, height .6s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.btn:hover:before {
  width: 300%;
  height: 300%;
}

.form-control {
  border: 2px solid #0000;
  transition: all .3s;
}

.form-control:focus {
  border-color: var(--secondary-color);
  box-shadow: 0 0 0 .2rem #2b6cb040;
}

.social-icons {
  gap: 1rem;
  display: flex;
}

.social-icon {
  background: var(--bg-light);
  width: 40px;
  height: 40px;
  color: var(--secondary-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  display: flex;
}

.social-icon:hover {
  background: var(--secondary-color);
  color: #fff;
  transform: translateY(-3px);
}

.loading-spinner {
  border: 3px solid #f3f3f3;
  border-top: 3px solid var(--secondary-color);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
